.stats {
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  width: 700px;
  margin-left: auto;
  margin-right: auto;
}

.stats > li {
  display: block;
  background: linear-gradient(to top, #0066aa, #ffffff);
  align-self: center;
  padding: 20px;
  border: 1px solid black;
}

.profile {
  text-align: center;
  background: linear-gradient(#0066aa, #ffffff);
  margin-left: auto;
  margin-right: auto;
  border: 5px solid black;
  border-radius: 10px;
  margin-top: 50px;
}

.username {
  color: white;
  font-size: 30px;
  font-weight: 700;
  text-shadow: 0px 0px 20px black;
}

.tag {
  color: blue;
  font-size: 22px;
  font-weight: 500;
}

.location {
  composes: tag;
  color: green;
  margin-bottom: 50px;
}

.label {
  display: block;
  font-size: 20px;
  margin-bottom: 5px;
  font-weight: 500;
  margin-bottom: 10px;
  color: black;
}

.quantity {
  composes: label;
  font-size: 22px;
  font-weight: 600;
  color: white;
  margin-bottom: 0;
  text-shadow: 0px 0px 10px black;
}
