.item {
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 0px 20px black;
  padding: 10px;
  border-radius: 5px;
}

.status {
  display: inline-flex;
  border-radius: 50%;
  margin-right: 15px;
  width: 10px;
  height: 10px;
}

.avatar {
  margin-right: 10px;
}

.name {
  font-size: 20px;
  font-weight: 600;
}
